import React, { useState } from 'react';
import './EmailSignup.css';

const EmailSignup = () => {
  const [email, setEmail] = useState('');
  const [status, setStatus] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    try {
      // Google Forms submission
      const formData = new FormData();
      formData.append('entry.1781149804', email); // Your form's entry ID
      
      await fetch('https://docs.google.com/forms/d/e/1FAIpQLSfVXgHrhPQQQt3LdEA8GBg84kL5DQWpERS-Oyk0F0V_arHgtw/formResponse', {
        method: 'POST',
        body: formData,
        mode: 'no-cors', // Important for Google Forms
      });

      // Google Forms with no-cors always appears successful
      setStatus('success');
      setEmail('');
    } catch (error) {
      setStatus('error');
    }
  };

  return (
    <div className="email-signup">
      <div className="signup-content">
        <h3>Newsletter</h3>
        
        {status === 'success' && (
          <div className="success-message">
            Email confirmed
          </div>
        )}
        
        {status === 'error' && (
          <div className="error-message">
            Something went wrong. Please try again.
          </div>
        )}
        
        <form onSubmit={handleSubmit} className="signup-form">
          <div className="input-row">
            <input
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              placeholder="email address"
              required
              className="email-input"
            />
            <button type="submit" className="signup-btn">
              Subscribe
            </button>
          </div>
        </form>
        
        <div className="subscription-disclaimer">
          By subscribing, you agree to receive news and updates from Zanjo Bloom and LVNDR Records.
        </div>
      </div>
    </div>
  );
};

export default EmailSignup; 